<script>
import * as pdfjsLib from "pdfjs-dist/build/pdf";
import pdfjsWorker from "pdfjs-dist/build/pdf.worker.entry";
import * as XLSX from "xlsx";

export default {
  name: "App",
  data() {
    return {
      parsedData: [],
      showBatchEditModal: false,
      batchCourse: "",
      batchUnit: "",
      isUploading: false,
      isLoggedIn: true,
      username: "PDFÈ¢òÂú®Á∫øËΩ¨Êç¢Á≥ªÁªü",
      password: "123456",
      fileName: "",
    };
  },
  mounted() {
    // this.setupColumnResize();
  },
  methods: {
    handleLogin() {
      // ÁÆÄÂçïÈ™åËØÅ
      if (this.username === "PDFÈ¢òÂú®Á∫øËΩ¨Êç¢Á≥ªÁªü" && this.password === "123456") {
        this.isLoggedIn = true;
      } else {
        alert("Áî®Êà∑ÂêçÊàñÂØÜÁ†ÅÈîôËØØ");
      }
    },
    async handleFileUpload(event) {
      this.isUploading = true;
      const file = event.target.files[0];
      this.fileName = file ? file.name : "Êú™ÈÄâÊã©Êñá‰ª∂"; // Â≠òÂÇ®Êñá‰ª∂Âêç

      setTimeout(async () => {
        this.isUploading = false;
        const file = event.target.files[0];
        if (file) {
          const arrayBuffer = await file.arrayBuffer();

          pdfjsLib.GlobalWorkerOptions.workerSrc = pdfjsWorker;

          try {
            const loadingTask = pdfjsLib.getDocument({
              data: arrayBuffer,
              disableAutoFetch: true,
              disableStream: true,
            });

            const pdfDoc = await loadingTask.promise;
            const numPages = pdfDoc.numPages;

            let allText = "";
            for (let i = 1; i <= numPages; i++) {
              const page = await pdfDoc.getPage(i);
              const textContent = await page.getTextContent();
              allText +=
                textContent.items.map((item) => item.str).join("\n") + "\n";
            }

            const filteredText = allText
              .replace(/ÁæøËøáÊïôËÇ≤ÂÆòÁΩë www\.yiguojy\.com ÁâàÊùÉÊâÄÊúâ/g, "")
              .replace(
                /‰∏ì‰∏öÁΩëÊ†°ËØæÁ®ã„ÄÅÈ¢òÂ∫ìËΩØ‰ª∂„ÄÅËÄÉËØïÁî®‰π¶„ÄÅËµÑËÆØ‰ø°ÊÅØÂÖ®Êñπ‰Ωç‰∏Ä‰ΩìÂåñËÅå‰∏öËÄÉËØïÂ≠¶‰π†Âπ≥Âè∞/g,
                ""
              );

            const questions = filteredText.split(/(?=\d+\.)/g);
            const parsed = [];

            questions.forEach((question) => {
              const lines = question.split("\n").filter((line) => line.trim());
              if (lines.length >= 3) {
                const options = lines
                  .slice(1, -2)
                  .filter(
                    (line) =>
                      !line.startsWith("ÂèÇËÄÉÁ≠îÊ°à") && !line.startsWith("„Äê")
                  )
                  .map((opt) => opt.trim());

                const analysisStartIndex = lines.findIndex((line) =>
                  line.startsWith("„ÄêÁæøËøáËß£Êûê„Äë")
                );

                console.log(analysisStartIndex, "Ëß£ÊûêÂïä");

                var analysisText = lines
                  .slice(analysisStartIndex)
                  .join("\n")
                  .split("„ÄêÁæøËøáËß£Êûê„Äë")[1];
                if (/\d$/.test(analysisText)) {
                  analysisText = analysisText.slice(0, -1);
                }
                // console.log(analysisText,'ÁúãÊàëÂïä');
                if (analysisText) {
                  if (analysisText.endsWith("ÈóÆÁ≠îÈ¢ò")) {
                    analysisText = analysisText.slice(0, -3);
                  }
                }
                const optionMap = {
                  A: "option1",
                  B: "option2",
                  C: "option3",
                  D: "option4",
                  E: "option5",
                  F: "option6",
                  G: "option7",
                  H: "option8",
                };

                const parsedOptions = {};
                options.forEach((opt) => {
                  const match = opt.match(/^([A-H])\.\s*(.*)/);
                  if (match) {
                    const [, key, value] = match;
                    // console.log(value, "valuevalue");
                    parsedOptions[optionMap[key]] = value;
                  }
                });
                const answerLine1 = lines.find((line) =>
                  line.startsWith("ÂèÇËÄÉÁ≠îÊ°àÔºö")
                );
                const answerText2 = answerLine1
                  ? answerLine1.split("ÂèÇËÄÉÁ≠îÊ°àÔºö")[1]
                  : "";

                // Â§ÑÁêÜÈ¢òÁõÆÂÜÖÂÆπ
                let questionContent = "";
                for (let i = 0; i < lines.length; i++) {
                  const line = lines[i];
                  if (line.startsWith("A.")) {
                    // Ëé∑ÂèñA.ÂâçÈù¢ÁöÑÊâÄÊúâË°å‰Ωú‰∏∫È¢òÁõÆÂÜÖÂÆπ
                    questionContent = lines
                      .slice(0, i)
                      .join("")
                      .replace(/^\d+\./, ""); // ÂéªÊéâÂâçÈù¢ÁöÑÂ∫èÂè∑
                    break;
                  }

                  if (
                    line.endsWith("„ÄÇ") ||
                    line.endsWith("Ôºâ") ||
                    line.endsWith("ÈóÆÔºö") ||
                    line.endsWith("Ôºü") ||
                    /^\(\d+\)/.test(line)
                  ) {
                    questionContent += line.replace(/^\d+\./, ""); // ÂéªÊéâÂâçÈù¢ÁöÑÂ∫èÂè∑
                    if (
                      line.endsWith("„ÄÇ") ||
                      line.endsWith("Ôºâ") ||
                      line.endsWith("ÈóÆÔºö") ||
                      line.endsWith("Ôºü")
                    ) {
                      break;
                    }
                  } else {
                    questionContent += line.replace(/^\d+\./, ""); // ÂéªÊéâÂâçÈù¢ÁöÑÂ∫èÂè∑
                  }
                }

                // È¢òÁõÆÂÜÖÂÆπÂ§ÑÁêÜ
                // const finalContent = 222;

                parsed.push({
                  course: "‰∏ìÂçáÊú¨-Ê≥ïÂ≠¶Á±ª",
                  unit: "Ê∞ëÊ≥ï",
                  type: "ÂçïÈÄâÈ¢ò",
                  content: questionContent,
                  answer: answerText2 == 'A' ? 1 : answerText2 == 'B'? 2 : answerText2 == 'C'? 3 : answerText2 == 'D'? 4 : answerText2 == 'E'? 5 : answerText2 == 'F'? 6 : answerText2 == 'G'? 7 : answerText2 == 'H'? 8 : "",
                  analysis: analysisText,
                  image: "",
                  option1: parsedOptions.option1 ? 1 : "" || "",
                  option2: parsedOptions.option2 ? 2 : "" || "",
                  option3: parsedOptions.option3 ? 3 : "" || "",
                  option4: parsedOptions.option4 ? 4 : "" || "",
                  option5: parsedOptions.option5 ? 5 : "" || "",
                  option6: parsedOptions.option6 ? 6 : "" || "",
                  option7: parsedOptions.option7 ? 7 : "" || "",
                  option8: parsedOptions.option8 ? 8 : "" || "",
                });
              }
            });

            this.parsedData = parsed;
          } catch (error) {
            console.error("PDFËß£ÊûêÂ§±Ë¥•:", error);
          }
        }
      }, 1500);
    },
    exportToXLSX() {
      if (this.parsedData.length === 0) {
        alert("Ê≤°ÊúâÊï∞ÊçÆÂèØ‰ª•ÂØºÂá∫ÔºÅ");
        return;
      }

      const headers = {
        course: "ËØæÁ®ã",
        unit: "ÂçïÂÖÉ",
        type: "È¢òÁõÆÁ±ªÂûã",
        content: "È¢òÁõÆÂÜÖÂÆπ",
        answer: "Ê≠£Á°ÆÁ≠îÊ°à",
        analysis: "Ëß£Êûê",
        image: "ÈÖçÂõæ",
        option1: "ÈÄâÈ°π1",
        option2: "ÈÄâÈ°π2",
        option3: "ÈÄâÈ°π3",
        option4: "ÈÄâÈ°π4",
        option5: "ÈÄâÈ°π5",
        option6: "ÈÄâÈ°π6",
        option7: "ÈÄâÈ°π7",
        option8: "ÈÄâÈ°π8",
      };

      const dataWithHeaders = this.parsedData.map((item) => {
        const newItem = {};
        for (const key in item) {
          newItem[headers[key]] = item[key];
        }
        return newItem;
      });

      const worksheet = XLSX.utils.json_to_sheet(dataWithHeaders);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Sheet1");

      const exportFileName = this.fileName
        ? this.fileName.replace(".pdf", ".xlsx")
        : "export.xlsx";
      // XLSX.writeFile(workbook, exportFileName);
      XLSX.writeFile(workbook, exportFileName);
    },
    applyBatchEdit() {
      this.parsedData.forEach((item) => {
        if (this.batchCourse) item.course = this.batchCourse;
        if (this.batchUnit) item.unit = this.batchUnit;
      });
      this.showBatchEditModal = false;
    },
  },
};
</script>

